import useCountryStats from '../utils/useCountryStats';

export default function Stats({ codeSelected }) {

  if (!codeSelected) return <p>select a country</p>;
  
  let url = `https://covid19.mathdro.id/api/countries/${codeSelected}`;
  const {stats, loading, error} = useCountryStats(url);

if (!stats) return <p>loading data for {codeSelected}</p>;

  console.log('country stats', stats);

  return (
    <div>
      <div className="statBlock">
        <h3>confirmed cases:</h3>
        <span>{stats.confirmed.value}</span>
      </div>
      <div className="statBlock">
        <h3>deaths:</h3>
        <span>{stats.deaths.value}</span>
      </div>
      <div className="statBlock">
        <h3>recoveries:</h3>
        <span>{stats.recovered.value}</span>
      </div>
    </div>
  )
}